import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import App from '../App.vue'
import detail from '../components/detail'
import detailanalysis from '../components/detail/analysis'
import detailcount from '../components/detail/count'
import detailforecast from '../components/detail/forecast'
import detailpublish from '../components/detail/publish'
Vue.use(Router)

export default new Router({
  routes: [
  
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/detail',
      name: 'detail',
      redirect:'/detail/analysis',
      component: detail,
      children: [
        {
          path: 'count',
          component: detailanalysis
        },
        {
          path: 'analysis',
          component: detailcount
        },
        {
          path: 'forecast',
          component: detailforecast
        },
        {
          path: 'publish',
          component: detailpublish
        }
      ]
    }
  ]
})



