<template>
  <div class="login-form" @keyup.enter="onLogin"  >
    <div class="g-form">
      <div class="g-form-line">
        <span class="g-form-label">用户名：</span>
        <div class="g-form-input">
          <input type="text"  
          v-model="usernameModel" placeholder="请输入用户名" v-focus> 
        </div>
        <span class="g-form-error">{{ userErrors.errorText }}</span>
      </div>
      <div class="g-form-line">
        <span class="g-form-label">密码：</span>
        <div class="g-form-input">
          <input type="password" 
          v-model="passwordModel" placeholder="请输入密码">
        </div>
        <span class="g-form-error">{{ passwordErrors.errorText }}</span>
      </div>
      <div class="g-form-line" @keyup.enter="onLogin">
        <div class="g-form-btn">
          <a class="button" @click="onLogin">登录</a>
        </div>
      </div>
      <p>{{ errorText }}</p>
    </div>
  </div>
</template>

<script>
export default {          
  directives: {       //自定义组件实现focus
    focus: {
      inserted: function (el) {
      // 聚焦元素
      el.focus()
      }
    }
  },
  data(){
    return {
      usernameModel: '',
      passwordModel: '',
      errorText: ''
    }
  },
  methods:{
    onLogin(){
      if(! this.userErrors.status || ! this.passwordErrors.status){
        this.errorText = "部分选项不正确"
      }else{
        this.errorText = ''
        

        this.$http.get('../../static/ab.json')
        .then((res) => {
          this.$emit('has-log', res.data)
        }, (error) => {
          console.log(error)
        })
      }
    }
  },
  computed:{
    userErrors(){
      let errorText,status
      if(!/@/g.test(this.usernameModel)){
        status = false
        errorText = "不包含@"
      }else{
        status = true
        errorText = ''
      }
      if(!this.useFlag){
        errorText = ''
        this.useFlag = true
      }
      return {
        status,
        errorText
      }
    },
    passwordErrors(){
      let errorText, status
      if (!/^\w{1,6}$/g.test(this.passwordModel)) {
        status = false
        errorText = '密码不是1-6位'
      }else{
        status = true
        errorText = ''
      }
      if(!this.passwordFlag){
        errorText = ''
        this.passwordFlag = true
      }
      return {
        status,
        errorText
      }
    }
  }
}
</script>

<style scoped>

</style>



